.list {
  display: flex;
  padding: 0;
  justify-content: space-around;
  &__item {
    text-align: center;
    border-right: 1px solid;
    flex-basis: 20%;
    text-decoration: none;
    color: black;
    &:focus {
      outline: none;
    }
    &:last-child {
      border: none;
    }
  }
  .home {
   color:  blue;
  }
  .about {
   color:  chartreuse;
  }
  .skills {
   color:  rebeccapurple;
  }
  .learning {
   color:  chocolate;
  }
  .contact {
   color:  salmon;
  }
}